/* ✅ 입력창 영역 */
.chat-input {
  display: flex;
  border-top: 1px solid #ccc;
  padding: 10px;
}

.chat-input input {
  flex: 1;
  padding: 10px 15px;
  border: 1px solid #ccc;
  border-radius: 20px;
  outline: none;
  box-sizing: border-box;
}

.chat-input button {
  padding: 10px 15px;
  border: none;
  border-radius: 20px;
  background-color: #4CAF50;
  color: white;
  cursor: pointer;
  margin-left: 10px;
  transition: background-color 0.3s ease;
}

.chat-input button:hover {
  background-color: #45a049;
}
