/* ✅ 버튼 영역 (가로 2열 배치 + 슬라이드 애니메이션) */
.button-options {
  padding: 10px;
  border-top: 1px solid #ccc;
  display: flex;
  flex-wrap: wrap;           /* ✅ 줄바꿈 가능 */
  justify-content: space-between;
  gap: 8px;
  animation: slideIn 0.6s ease;
}

@keyframes slideIn {
  0% {
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

/* ✅ 버튼 공통 스타일 */
.button-options button {
  flex: 0 1 calc(50% - 8px);   /* ✅ 가로 2개씩 정렬 */
  padding: 10px 15px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  font-weight: bold;
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  transition: background-color 0.3s ease;
}

/* ✅ 초록 버튼 */
.btn-green {
  background-color: #4CAF50;
  color: white;
}

.btn-green:hover {
  background-color: #45a049;
}

/* ✅ 주황 버튼 */
.btn-orange {
  background-color: #FF9800;
  color: white;
}

.btn-orange:hover {
  background-color: #fb8c00;
}
